<template>
  <div class="box">
    <input type="text" id="success_form_input" readonly="readonly" v-model="link"/>
    <button ref="copy" data-clipboard-action="copy" data-clipboard-target="#success_form_input" @click="copyLink">复制</button>
    <div class="tip-box" :class="tip == '复制成功'?'success': tip == '复制失败'?'error':''" v-show="tipShow">{{tip}}</div>
  </div>
</template>

<script>
  import clipboard from 'clipboard';
  import axios from 'axios'
  export default {
    name: "copy_demo",
    data() {
      return {
        copyBtn: null ,//存储初始化复制按钮事件,
        link:'qwerwer',
        tipShow:true,
        tip:''
      }
    },
    mounted() {
      this.copyBtn = new clipboard(this.$refs.copy);
      axios.post(this.proxy + 'queryByUser.do').then(res => {
        if (res.status == 200 && res.data.error_no == '0') {
          let data = res.data.data;
          this.link = data.create_date;
        }
      })
    },
    methods: {
      copyLink() {
        let clipboard = this.copyBtn;
        clipboard.on('success', (res)=> {
          this.tip = '复制成功'
          this.tipShow = true;
        });
        clipboard.on('error', (res)=> {
          this.tip = '复制失败'
          this.tipShow = true;
        });
        this.disappear();
      },
      disappear(){
        setTimeout(()=>{
          this.tipShow = false;
        },500)
      }
    }
  }
</script>

<style scoped lang="stylus">
  .box{
    position absolute
    top 0
    bottom 0
    right 0
    left 0
    margin auto;
    background url("../../assets/lottery-bg.png") no-repeat center center
    backgrond-size cover
  }
  #success_form_input{
    border 0
    outline none;
    background transparent;
  }
  .tip-box{
    width 2rem
    height .5rem
    line-height .5rem
    font-size .18rem
    color #fff
    background #be3a14
    position absolute
    top 2rem
    left 0
    right 0
    margin-left auto
    margin-right auto
    text-align center

  }
</style>



